
body {
  overflow: hidden;
  margin: 0;
  background-color: #colors[base1];
  font-family: Helvetica, sans-serif;
  color: #colors[secondary];
}


#appContainer {
  height:100vh;
  width: 100vw;
  /* display: grid;
  grid-template-columns: 50% auto;
  grid-template-rows: 80% auto; */
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: @topNavHeight auto;

  #appPrimary {
    flex-basis: auto;
    display: flex;
    flex-direction: row;

    #inputContainer {
      /* flex-basis: 50%; */

    };

    #outputContainer {
      /* flex-basis: 50%; */
      display: flex;
      flex-direction: column;

      #renderOutputContainer {
        /* grid-column: 2/2  ;
        grid-row: 1/1; */

        overflow: auto;

        iframe {
          height: 100%;
          width: 100%;
        }
      };
      #errorsOutputContainer {
        /* grid-column: 2/2  ;
        grid-row: 2/2; */
        overflow:auto;
      };
    };
  };
}

.navbar {
  * {
    float: left;
  }
  h1 {
    font-size: 1em;
    margin: 0;
    height: 100%;
    padding-left: 1em;
    padding-right: 1em;
  }
  button {
    height: 100%;
    border-top: 0;
    border-right: 0;
    border-left: 0;

  }
  div {
    height: 100%;
  }
}

.editorContainer {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;

  &.hidden {
    display: none;
  }

  .editor {
      // width: 100%;
      // height: 100%;
      position: relative;
      flex-basis: 100%;
  }
}

.output-log {
  padding-left: 4px;
  padding-right: 4px;
  font-size: 0.8em;
  font-family: monospace;
  .log-entry {
    padding-left: 16px;
    padding-top: 3px;
    padding-bottom: 3px;
    border-bottom: 1px solid #colors[secondary];
    &.LOG {

    }
    &.ERROR {
      color: red;
    }
    &.DEBUG {

    }
  }
}

.modal.about {
  .modal-content {
    div {
      padding-top: 5px;
      padding-bottom: 5px;
    }

  }
}

.credits-list {
  li {
    padding-top: 5px;
    padding-bottom: 5px;
    span {

    }

    a {
      padding-left: 1em;
    }
  }
}
