.switch-slider {

  position: relative;
  display: block;
  margin-left: 0.5em;
  margin-right: 0.5em;
  margin-top: auto;
  margin-bottom: auto;

  input {
    opacity: 0;
    width: 0;
    height: 0;
  };

  label {
    height: 100%;

    .slider {
      position: relative;
      display: inline-block;
      position: relative;
      width: 2em;
      height: 1em;
      border-radius: 0.5em;
      background: #colors[secondary];

      &:before {
          position: absolute;
          width: 0.8em;
          height: 0.8em;
          background: #colors[accent1];
          border-radius: 0.5em;
          content: "";
          border-radius: 8px;
          border: 0.1em solid #colors[secondary];
          left: 0px;
        }
    }

    .label {
      display: inline-block;
      padding-left: 0.5em;
    }

  }


  input:checked + label > .slider:before {
    background: #colors[primary];
    left: 16px;
  }
}

button {
  border: 2px solid #colors[primary];
  padding: 0.2em;
  margin: 0.2em;

  background-color: transparent;

  &:hover {
    background-color: #colors[primary];
  }

  &.primary {
    border-color: #colors[primary];

    &:hover {
      background-color: #colors[primary];
    }
  }

  &.active {
    background-color: #colors[accent2];
  }


}


.modal {
 display: none;
 position: fixed;
 visibility: hidden;
 z-index: 1;
 left: 0;
 top: 0;
 width: 100%;
 height: 100%;
 overflow: auto;
 background-color: #colors[base1];
 background-color: fade(#colors[base1],50%);

 &.active {
   display: block;
   visibility: visible;
 }

 .modal-content {
  background-color: #colors[accent1];
  margin: 30px auto;
  padding: 5px;
  border: 1px solid #888;
  width: 80%;

  .modal-header {
    padding: 2px 16px;
    background-color: #colors[secondary];
    color: #colors[primary];

    display: flex;

    h1 {
      flex-basis: 100%;
    }

    .modal-close {
     font-size: 28px;
     font-weight: bold;
     i {
       color: #colors[primary];
     }
    }
  }

  .modal-body {
    padding: 2px 16px;
    color: #colors[secondary];
  }

  .modal-footer {
    padding: 2px 16px;
    background-color: #colors[secondary];
    color: #colors[primary];
  }


 }
}
